.telaPrincipal {
  display: grid;
  place-items: center;
  overflow: hidden;
  color: yellow;
  background: #000;
  height: 100vh;
  width: 100vw;
}

.letreiro {
  display: flex;
  justify-content: center;
  height: 100vh;
  perspective: 20rem;
  color: #feda4a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 200%;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  text-align: center;
}

.crawl {
  position: relative;
  transform-origin: 50% 100%;
  animation: crawl 60s linear;
}

@keyframes crawl {
  0% {
      top: 0;
      transform: rotateX(20deg) translateZ(0);
  }
  100% {
      top: -6000px;
      transform: rotateX(25deg) translateZ(-2500px);
  }
}

@media (min-width: 680px) {
  .letreiro {
    font-size: 500%;
  }
}
